import myPicker from "@/components/myPicker.vue";
import myCheckboxGroup from "@/components/myCheckboxGroup.vue";
import uniIcon from "@/components/uni-icons/uni-icons.vue";
const JsyServer = require("services/jsy-server.js");
let _this,_operateCode;
export default {
	components:{
		myPicker,
		myCheckboxGroup,
		uniIcon
	},
	data() {
		return {
			months:[
				{id: 1,label:'一月'},
				{id: 2,label:'二月'},
				{id: 3,label:'三月'},
				{id: 4,label:'四月'},
				{id: 5,label:'五月'},
				{id: 6,label:'六月'},
				{id: 7,label:'七月'},
				{id: 8,label:'八月'},
				{id: 9,label:'九月'},
				{id: 10,label:'十月'},
				{id: 11,label:'十一月'},
				{id: 12,label:'十二月'}
			],
			highSeasonTime:'',

			operateCapital:[],
			capitalCode: -1,
			capitalName: '',
			
			operateWom:[],
			womCode: -1,
			womName:'',
			
			operateCredit:[],
		    creditCode: -1,
			creditName: '',
			
			operateOperation:[],
		    operationCode: -1,
			operationName:''
			
		};
	},
	
	onLoad:function(options){
		_this = this;
		_operateCode = JSON.parse(options.data)
		console.log(_operateCode)
		this.getOperateCapital()
		this.getOperateWom()
		this.getOperateCredit()
		this.getOperateOperation()
		
	},
	onShow:function(){
		
		// _this.operateWom.forEach((item)=>{
		// 	if (item.label ==_operateCode.womName){
		// 		_this.womCode = item.id
		// 	}
		// })
		
		//旺季时间，字符串拆成数组
		let season = _operateCode.highSeasonTime.split(',')
		season.forEach((s) =>{
			this.months.forEach((item)=>{
				if (item.label == s){
					this.$set(item,'isChecked',true)
				}
			})
		})
		_this.capitalName = _operateCode.capitalName
		_this.creditName = _operateCode.creditName
		_this.womName = _operateCode.womName
		_this.operationName = _operateCode.operationName
			
		
		
	},

	methods:{
		getOperateCapital:function(){
			JsyServer.getOperateCapital().then(res => {
			  console.log(res);
			  _this.operateCapital = res.data.data.list
			}).catch(err => {
			  wx.showToast({
			    title: err.data.description,
			    icon: 'none'
			  });
			});
		},
		getOperateWom:function(){
			JsyServer.getOperateWom().then(res => {
			  console.log(res);
			  _this.operateWom = res.data.data.list
			}).catch(err => {
			  wx.showToast({
			    title: err.data.description,
			    icon: 'none'
			  });
			});
		},
		getOperateCredit:function(){
			JsyServer.getOperateCredit().then(res => {
			  console.log(res);
			  _this.operateCredit = res.data.data.list
			}).catch(err => {
			  wx.showToast({
			    title: err.data.description,
			    icon: 'none'
			  });
			});
		},
		getOperateOperation:function(){
			JsyServer.getOperateOperation().then(res => {
			  console.log(res);
			  _this.operateOperation = res.data.data.list
			}).catch(err => {
			  wx.showToast({
			    title: err.data.description,
			    icon: 'none'
			  });
			});
		},
		
		tapMonths:function(index){
			let items = this.months
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
				const item = items[i]
				if (item.id == index) {
					this.$set(item,'isChecked',!item.isChecked)
				}
			}
		},
		tapPickerEvent:function(arrName,id){
			switch (arrName){
				case "operateCapital":
                   console.log(id)
				   this.capitalCode = id
				   break;
				case "operateWom":
				  this.womCode = id
				  break;
				case "operateCredit":
				  this.creditCode = id
				  break;
				case "operateOperation":
				  this.operationCode = id
				  break;  
			}
		},
		
		
		bindCancel:function(){
			uni.navigateBack({
				delta: 1
			});
		},
		submit:function(e){
			let highSeasonTime = []
			
			this.months.forEach((item)=>{
				if (item.isChecked== true){
					highSeasonTime.push(item.label)
				}
			})
			
			if (highSeasonTime.length == 0){
				uni.showToast({
					title: '请选择旺季时间',
					icon: 'none'
				});
				return;
			}
			
			let before_data= {
			     operateCode: _operateCode.operateCode,			//客户编码
			     highSeasonTime: highSeasonTime.join(','),		//旺季时间
			     capitalCode: this.capitalCode,			//资金状况编码
			     creditCode: this.creditCode,			//信用度编码
			     womCode: this.womCode,				//口碑
			     operationCode: this.operationCode
				}
		    console.log(before_data)
			let _data={}
			//过滤未选择的项
			Object.keys(before_data).forEach((key)=>{
				if (before_data[key] != -1){
					_data[key] = before_data[key]
				}
			})
			console.log(_data)
		        
			JsyServer.operationUpdate(_data).then(res => {
			  console.log(res);
			  if (res.data.status == 0){
				  var pages = getCurrentPages();
				  var currPage = pages[pages.length - 1]; //当前页面
				  var prevPage = pages[pages.length - 2]; //上一个页面
				  //直接调用上一个页面的setData()方法，把数据存到上一个页面中去
				  prevPage.setData({
						isDoRefresh:true
				  })
				  uni.navigateBack({
					delta: 1
				  });
			  }else{
				  uni.showToast({
						title: res.data.message,
						icon: 'none'
				  });
			  }
			}).catch(err => {
			  uni.showToast({
				title: err.data.description,
				icon: 'none'
			  });
			});

			
		}
		
	  }
}
